/* modules */
.content {
  padding-bottom: 120px;
  &__head {
    text-align: center;
    margin: 0 auto;
    .h2, h2 {
      .span {
        color: #e20b0b;
      }
    }
  }
}

.main-section {
  background: #ae0101 url('../images/bg-main-section.png') bottom center no-repeat;
  mask: url('../images/bg-main-section.png');
  mask-position: bottom center;
  color: $color-white;
  // min-height: 990px;
  padding-top: 145px;
  height: 990px;
  @include clearfix;
  &__head {
    text-align: center;
    .h1, h1 {
      color: $color-white;
    }
  }
  &__untitle {
    font-weight: 500;
    font-size: rem(32px);
    margin-bottom: 30px;
    strong {
      color: #ffe534;
    }
  }
  &__wrapper {
    display: flex;
    margin-bottom: 60px;
    padding-left: 116px;
  }
  &__form {
    width: 434px;
    min-height: 617px;
    background: #ffe745;
    border-radius: 35px;
    padding: 35px 55px 20px 50px;
    order: 1;
    box-shadow: -7px -7px 0 #ffa21e;
    color: $primary-color;
    &-content {
      font-size: rem(18px);
      font-weight: 500;
      ul {
        padding-left: 18px;
        margin: 0;
        padding-bottom: 30px;
        li {
          line-height: 1.2;
          &+li {
            margin-top: 10px;
          }
        }
      }
    }
    &-head {
      font-weight: 900;
      font-size: rem(25px);
      line-height: 1;
      margin-bottom: 25px;
      strong {
        font-size: rem(35px);
        font-weight: 900;
      }
    }
  }
  &__img {
    order: 2;
    padding-left: 19px;
    position: relative;
  }
  &__img-1 {
    position: absolute;
    bottom: 5px;
    margin-left: 190px;
  }
  &__img-2 {
    position: absolute;
    bottom: -45px;
  }
}

.bg-img {
  position: relative;
  height: 0;
  z-index: 5;
  &__wrapper {
    width: 100%;
    left: 0;
    top: 0;
    position: absolute;
  }
  &__img {
    top: 0;
    position: absolute;
    left: 0;
    pointer-events: none;
    &--top-left {
      transform: translateX(calc(-100% - 35px));
      margin-top: -75px;
    }
    &--top-right {
      left: 100%;
      transform: translateX(20px);
      margin-top: 0px;
    }
    &--ms1 {
      margin-left: -122px;
      margin-top: 22px;
    }
    &--ms2 {
      margin-left: -500px;
      margin-top: 90px;
    }
    &--about-left {
      margin-top: -200px;
      margin-left: -1100px;
    }
    &--about-right {
      margin-top: -350px;
      margin-left: 40px;
      left: 100%;
    }
    &--school-right {
      left: 100%;
      margin-top: -518px;
      margin-left: 110px;
    }
    &--comments-left {
      transform: translateX(-100%);
      margin-top: -240px;
    }
  }
}

.about {
  padding-top: 95px;
  padding-bottom: 90px;
  background: #fafafa;
  &__head {
    .h2, h2 {
      margin-bottom: 95px;
    }
  }
  &__wrapper {
    display: flex;
    margin-bottom: 120px;
    &-img {
      flex-grow: 1;
    }
    &-content {
      width: 457px;
      flex-shrink: 0;
      padding-left: 10px;
      font-size: rem(25px);
      font-weight: 600;
      ul {
        margin: 0;
        padding: 0;
        li {
          padding: 10px 20px;
          list-style: none;
          margin: 0;
          display: inline-block;
          background: #ffe160;
          &+li {
            margin-top: 26px;
          }
          &:first-child {
            max-width: 422px;
          }
          &:nth-child(2) {
            max-width: 288px;
          }
        }
      }
    }
  }
  &__img {
    margin-left: -111px;
    position: relative;
    display: inline-block;
    img {
      clip-path: polygon(0 0,100% 12%,96% 85%,14% 100%);
      position: relative;
      z-index: 5;
      background: pink;
    }
    &::before {
      content: '';
      position: absolute;
      left: 21px;
      top: 13px;
      background: url('data:image/png;base64,') 0 0 no-repeat;
      width: 765px;
      height: 401px;
    }
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      background: url('data:image/png;base64,') 0 0 no-repeat;
      width: 778px;
      height: 403px;
      margin-left: -40px;
      margin-top: -30px;
      left: 0;
      top: 0;
    }
  }
}

.form-once {
  width: 100%;
  max-width: 970px;
  margin: 0 auto;
  background: linear-gradient(to bottom, #cf1919, #930101);
  color: $color-white;
  box-shadow: -4px -4px 0 #8e0101;
  padding: 27px 125px 35px 117px;
  border-radius: 50px;
  &__head {
    font-weight: 600;
    font-size: rem(25px);
    line-height: 1;
    margin-bottom: 36px;
    text-align: center;
    strong {
      font-size: rem(40px);
      font-weight: 800;
    }
  }
  &__row1 {
    display: flex;
    &>div {
      &+div {
        margin-left: 22px;
      }
      &.div1 {
        width: 177px;
      }
      &.div2 {
        width: 207px
      }
      &.div3 {
        flex-grow: 1;
      }
    }
  }
  &__row2 {
    display: flex;
    &>div {
      &+div {
        margin-left: 56px;
      }
      &.div1 {
        width: 328px;
      }
      &.div2 {
        flex-grow: 1;
      }
    }
  }
  .form-group {
    margin-bottom: 19px;
  }
  a {
    color: $color-white;
    &:hover {
      color: $color-white;
    }
  }
  .form-control {
    &+.error {
      color: $primary-color;
    }
  }
}

.video {
  padding-top: 110px;
  padding-bottom: 120px;
  height: 1300px;
  background: url('../images/bg-video.png') top center no-repeat;
  &__head {
    max-width: 810px;
  }
  &__content {
    text-align: center;
    font-size: rem(27px);
    font-weight: 600;
    max-width: 955px;
    margin: 0 auto 85px;
    p {
      &:last-child {
        margin-bottom: 0;
      }
      span {
        background: #fff553;
        display: inline-block;
        padding: 5px;
        line-height: 1.1;
      }
    }
  }
  &__block {
    width: 524px;
    display: block;
    margin-bottom: 50px;
    position: relative;
    img {
      position: relative;
      z-index: 3;
      max-width: 100%;
      height: auto;
      display: block;
    }
    &::before {
      content: '';
      position: absolute;
      background: url('../images/video-block-bg.png') 0 0 no-repeat;
      width: 622px;
      height: 292px;
      right: 0;
      top: -43px;
      z-index: 1;
      transform-origin: 50%;
    }
    
    &-shadow {
      position: absolute;
      background: rgba($color-black,.75);
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      transition: all 0.35s;
      z-index: 5;
      &::after {
        content: '';
        position: absolute;
        left: 18px;
        right: 18px;
        top: 18px;
        bottom: 18px;
        border: 3px solid $color-white;
        z-index: 5;
        transition: all 0.35s;
      }
    }
    &-play {
      background: url('data:image/png;base64,') 0 0 no-repeat;
      position: absolute;
      z-index: 8;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      width: 82px;
      height: 81px;
    }
    &:hover {
      .video__block-shadow {
        background: rgba($color-black,.85);
        &::after {
          border-color: #fee966;
        }
      }
    }
  }
  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    &>div {
      padding: 0 15px;
      &:nth-child(2) {
        .video__block {
          &::before {
            transform: rotateY(-180deg);
            left: 0;
          }
        }
      }
      &:nth-child(3) {
        .video__block {
          &::before {
            transform: rotateX(-180deg);
            bottom: -43px;
            top: inherit;
          }
        }
      }
      &:nth-child(4) {
        .video__block {
          &::before {
            transform: rotate(-180deg);
            left: 0;
            bottom: -43px;
            top: inherit;
          }
        }
      }
    }
  }
  &__more {
    width: 709px;
    height: 97px;
    white-space: nowrap;
    text-align: center;
    font-weight: 600;
    font-size: rem(30px);
    padding: 18px 0;
    background: url('data:image/png;base64,') 0 0 no-repeat;
    b {
      font-weight: 900;
      color: #ce1919;
    }
    margin: 0 auto;
    letter-spacing: -0.3px;
    a {
      display: inline-block;
      vertical-align: 4px;
    }
  }
}

.school {
  padding-top: 80px;
  &__untitle {
    font-size: rem(27px);
    text-align: center;
    margin: 0 auto 100px;
    max-width: 945px;
    font-weight: 500;
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__wrapper {
    display: flex;
    &-content {
      width: 585px;
      padding-right: 30px;
      flex-shrink: 0;
    }
    &-img {
      flex-grow: 1;
    }
  }
  &__img {
    width: 1495px;
    height: 344px;
    background: linear-gradient(to bottom, #d21a1a, #910000);
    position: relative;
    border-radius: 170px;
    img {
      position: absolute;
      bottom: 0;
      left: 56px;
    }
  }
  &__content {
    font-size: rem(21px);
    margin-left: 83px;
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: none;
        position: relative;
        padding-left: 48px;
        min-height: 28px;
        margin: 0 0 23px 0;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}

.comments {
  padding-top: 100px;
  padding-bottom: 105px;
  &__head {
    .h2, h2 {
      margin-bottom: 100px;
    }
  }
  &__wrapper {
    max-width: 810px;
    margin: 0 auto;
  }
  &__block {
    display: flex;
    align-items: center;
    font-size: rem(18px);
    font-weight: 500;
    &+[class*="__block"] {
      margin-top: 80px;
    }
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
    img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      object-fit: cover;
      flex-shrink: 0;
    }
    &-content {
      padding-left: 50px;
    }
    &-title {
      font-weight: bold;
      margin-bottom: 10px;
    }
  }
}

.learn {
  padding-top: 60px;
  &__head {
    .h2, h2 {
      margin-bottom: 50px;
    }
  }
}

.icon-social {
  position: absolute;
  left: 0;
  top: 2px;
  width: 18px;
  height: 18px;
  background: url('../images/social-sprites.png') -0 -54px no-repeat;
  &--email {
    background-position: -0 -18px;
  }
  &--telegram {
    background-position: -0 -36px;
  }
  &--youtube {
    background-position: -0 -0;
  }
}